                                                <input type="number" class="form-control" placeholder="Telefono" aria-label="Telefono" aria-describedby="basic-addon2">
                                                <div class="input-group-append">
                                                    <button class="btn btn-outline-danger" type="button" @click="removeTelefono(index)" v-if="index+1 != contactos.length">
                                                        <i class="fas fa-minus"></i>
