                                                    <button class="btn btn-outline-primary" type="button" @click="addTelefono" v-if="index+1 == contactos.length">
                                                        <i class="fas fa-plus"></i>
                                                    </button>
                                                </div>
                                        </div>
                                    </div> -->

                  <!-- <div class="col-6">
                                        <div class="input-group mb-3 mt-3" v-for="(contacto,index) in contactos" :key="contacto.consecutivo">  
                                                <input type="number" class="form-control" placeholder="Corrreo" aria-label="Corrreo" aria-describedby="basic-addon2">
                                                <div class="input-group-append">
                                                    <button class="btn btn-outline-danger" type="button" @click="removeCorrreo(index)" v-if="index+1 != contactos.length">
                                                        <i class="fas fa-minus"></i>
                                                    </button>
                                                    <button class="btn btn-outline-primary" type="button" @click="addCorrreo" v-if="index+1 == contactos.length">
                                                        <i class="fas fa-plus"></i>
                                                    </button>
                                                </div>
                                        </div>
                                    </div> -->
                </div>
              </b-card>
            </div>
          </b-col>
          <!-- </b-form> -->
        </div>
      </b-tab>

      <!-- CREDITO -->
      <b-tab :active="activeCredito" v-if="permisoCobranza">
