                  <b-badge
                    variant="success"
                    v-if="row.item.estatus === 'T'"
                    class="p-2"
                  >
                    Terminado
                  </b-badge>
                  <b-badge
                    variant="secondary"
                    v-if="row.item.estatus === 'P'"
                    class="p-2"
                  >
                    Pendiente
                  </b-badge>
                  <b-badge
                    variant="secondary"
                    v-if="row.item.estatus === 'PC'"
                    class="p-2"
                  >
                    Pendiente de Confirmar
                  </b-badge>
                  <b-badge
                    variant="info"
                    v-if="row.item.estatus === 'S'"
                    class="p-2"
                  >
                    Surtiendo
                  </b-badge>
                  <b-badge
                    variant="danger"
                    v-if="row.item.estatus === 'C'"
                    class="p-2"
                  >
                    Cancelado
                  </b-badge>
                  <b-badge
                    variant="warning"
                    v-if="row.item.estatus === 'DA'"
                    class="p-2"
                  >
                    Pendiente Autorizacion
                  </b-badge>
                  <b-badge
                    variant="warning"
                    v-if="row.item.estatus === 'DC'"
                    class="p-2"
                  >
                    Pendiente Limite Credito
                  </b-badge>
                </template>

                <template v-slot:cell(acciones)="row">
                  <b-button
                    @click="verInformacion(row.item)"
