  async mounted() {
   
    await this.VerificarPermisoCobranza();
    await this.VerificarPermisoHistorial();
    await this.ObtenerSegmentos();
    await this.ObtenerTiposClientes();
    await this.ObtenerTiposClienteCredito();
    await this.ObtenerTiposListaPrecios();
    // await this.CargarDefault();

    if (this.id > 0) {
      await this.cargaCliente();
      await this.obtenerDocumentos();
    }

    this.activeGeneral = true;
    this.options = null;
    this.permisoEscritura = true;

    if (this.permisoHistorial) {
      await this.buscarHistorial();
    }
  },

  async created() {
    this.id = this.$route.params.id != undefined ? this.$route.params.id : 0;
    
    if (this.usuario.agente === 1) {
      this.vendedores = [
        { value: this.usuario.codEmpleado, text: this.usuario.nombre },
      ];
      this.form.codVendedor = this.vendedores[0].value;
      this.disabledVendedor = true;
    } else {
      this.disabledVendedor = false;
      await this.obtenerVendedores();
    }
  },

  computed: {
    validationNombres() {
      let letras = /^[a-zA-Z\u00C0-\u00FF]*$/;
      return this.nombre.match(letras) && this.nombre.length > 1 ? true : false;
    },
    validationApellidoP() {
      let letras = /^[a-zA-Z\u00C0-\u00FF]*$/;
      return this.apellidoP.match(letras) && this.apellidoP.length > 1
        ? true
        : false;
    },
    searchLocationObra() {
      let colonia = "";
      let currentCodColonia = this.form.direccionObra.codColonia;
      if (currentCodColonia != null) {
        colonia = this.colonias.find((el) => {
          return el.value == currentCodColonia;
        });
      }
      return `${this.form.direccionObra.calleNumero} ${
        colonia?.text == undefined ? "" : colonia?.text
      }`;
    },
    searchLocationFiscal() {
      let colonia = "";
      let currentCodColonia = this.form.direccionFiscal.codColonia;
      if (currentCodColonia != null) {
        colonia = this.coloniasFiscales.find((el) => {
          return el.value == currentCodColonia;
        });
      }
      return `${this.form.direccionFiscal.calleNumero} ${
        colonia?.text == undefined ? "" : colonia?.text
      }`;
    },
  },

  watch: {
    "form.direccionFiscal.cP": function (newValue, oldValue) {
      if (newValue != oldValue) {
        this.form.direccionFiscal.codColonia = null;
        if (newValue.length == 5) {
          this.obtenerDatosCP(true);
        } else {
          this.coloniasFiscales = [
            { value: null, text: "Seleccione una colonia..." },
          ];
          this.form.direccionFiscal.municipio = "";
        }
      }
    },
    "form.direccionObra.cP": function (newValue, oldValue) {
      if (newValue != oldValue) {
        this.form.direccionObra.codColonia = null;
        if (newValue.length == 5) {
          this.obtenerDatosCP(false);
        } else {
          this.colonias = [{ value: null, text: "Seleccione una colonia..." }];
          this.form.direccionObra.municipio = "";
        }
      }
    },
    "form.rFC": function (newValue) {
      if (this.form.rFC != undefined) {
        this.form.rFC = this.form.rFC.toUpperCase();
        if (
          /^([A-Z,Ñ,&]{3,4}([0-9]{2})(0[1-9]|1[0-2])(0[1-9]|1[0-9]|2[0-9]|3[0-1])[A-Z|\d]{3})$/.test(
            newValue
          )
        ) {
          this.form.rFCValidate.esValido = true;
          this.form.rFCValidate.msg = "El RFC es válido";
        } else {
          this.form.rFCValidate.esValido = false;
          this.form.rFCValidate.msg = "El RFC NO es válido";
        }
      }
    },
    "form.rfcGenerico": function (newValue) {
      this.form.rFC = newValue ? "XAXX010101000" : "";
    },
  },

