    agregarDocumentos() {
      this.documentoCargado = {
        codigo: 0,
        titulo: "",
        descripcion: "",
        documento: "",
      };
      this.modalDocumentoShow = true;
    },
    guardarDocumento() {
      if (this.$refs.file.files[0] != undefined) {
        this.documentoCargado.documento = this.$refs.file.files[0];
        this.lstDocumentos.push(this.documentoCargado);
        this.modalDocumentoShow = false;
        this.agregaDocumento = false;
        this.tituloBtnDocumentos = "Mostrar Documentos";
      } else {
        this.$swal.fire({
          text: `Seleccione un documento para continuar`,
          icon: "info",
        });
      }
    },
    async eliminarDocumento(indice) {
      let msgRes = await this.$swal.fire({
        title: "¡ATENCIÓN!",
        text: `¿Está seguro que desea eliminar el documento seleccionado? `,
        showCancelButton: true,
        icon: "question",
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
        confirmButtonText: "Aceptar",
        cancelButtonText: "Cancelar",
      });

      if (msgRes.value) {
        if (this.lstDocumentos[indice].codigo > 0) {
          let r = await servicio.eliminaDocumento(
            this.lstDocumentos[indice].codigo
          );
          if (r.value) {
            this.$swal.fire({
              text: r.message,
              icon: "info",
            });
            this.lstDocumentos.splice(indice, 1);
          }
        } else {
          this.lstDocumentos.splice(indice, 1);
        }

        if (this.lstDocumentos.length == 0) {
          this.agregaDocumento = true;
        }
      }
    },
    async mostrarDocumento(indice) {
      if (this.lstDocumentos[indice].codigo > 0) {
        var binaryString = window.atob(this.lstDocumentos[indice].documento);
        var binaryLen = binaryString.length;
        var bytes = new Uint8Array(binaryLen);
        for (var i = 0; i < binaryLen; i++) {
          var ascii = binaryString.charCodeAt(i);
          bytes[i] = ascii;
        }

        var blob = new Blob([bytes], { type: "application/pdf" });
        var link = window.URL.createObjectURL(blob);
        window.open(link, "", "height=650,width=840");
      } else {
        var reader = new FileReader();
        reader.readAsDataURL(this.lstDocumentos[indice].documento);
        reader.onload = function () {
          var base64URL = reader.result;
          var binaryString = window.atob(
            base64URL.replace("data:application/pdf;base64,", "")
          );
          var binaryLen = binaryString.length;
          var bytes = new Uint8Array(binaryLen);
          for (var i = 0; i < binaryLen; i++) {
            var ascii = binaryString.charCodeAt(i);
            bytes[i] = ascii;
          }
          var blob = new Blob([bytes], { type: "application/pdf" });
          var link = window.URL.createObjectURL(blob);
          window.open(link, "Documento", "height=650,width=840");
        };
      }
    },
    async obtenerDocumentos() {
      this.$loading(true)
      let r = await servicio.documentosCliente(this.id);
      if (r.value) {
        this.tituloBtnDocumentos = "Mostrar Documentos";
        this.lstDocumentos = r.data;
        this.agregaDocumento = false;
      }
      this.$loading(false)
    },
    enviarWs(item) {
      if (item.telefonoMovil) {
        window.open(
          `https://api.whatsapp.com/send?phone=52${item.telefonoMovil}`,
          "_blank"
        );
      } else
        this.$swal.fire({
          text: "No existe teléfono móvil asociado",
          icon: "warning",
        });
    },
    llamar(item) {
      if (item.telefono) {
        window.open(`tel:+52${item.telefono}`, "_blank");
      } else if (item.telefonoMovil) {
        window.open(`tel:+52${item.telefonoMovil}`, "_blank");
      } else {
        this.$swal.fire({
          text: "No existe telefono asociado",
          icon: "warning",
        });
      }
    },
    enviar(item) {
      if (item.correo) {
        window.open(`mailto:${item.correo}`);
      } else {
        this.$swal.fire({
          text: "No existe correo asociado",
          icon: "warning",
        });
      }
    },

