    async guardarCliente() {
      let r = await this.$swal.fire({
        text: "¿Desea guardar el usuario?",
        icon: "question",
        showCancelButton: true,
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
        confirmButtonText: "Aceptar",
        cancelButtonText: "Cancelar",
      });

      if (r.value) {
        if (this.validarRegistros()) {
          this.activeCredito = true;
          this.$swal.fire({
            text: `Hay campos en el apartado de cobranza por completar`,
            icon: "error",
          });
        }

        this.puedeGuardarCliente = true;
        if (this.puedeGuardarCliente) {
          let cliente = {
            codigo: this.id,
            nombre: this.form.nombre,
            nombreCompleto: "",
            apellidoP: this.form.apellidoP,
            apellidoM: this.form.apellidoM,
            rFC: this.form.rFC,
            regimenFiscal: this.form.regimenFiscal,
            alias: this.form.alias,
            telefono: this.form.telefono,
            celular: this.form.celular,
            correo: this.form.correo,
            nombreComercial: this.form.nombreComercial,
            razonSocial: this.form.razonSocial,
            codEmpleadoVendedor: this.form.codVendedor,
            codTipoCliente: this.form.codTipoCliente,
            codSegmento: this.form.codSegmento,
            codTipoClienteCredito: this.form.codTipoClienteCredito,
            codTipoListaPrecio: this.form.codTipoListaPrecio,
            diaRevision: this.form.diaRevision,
            facturarPublicoGeneral: this.form.rfcGenerico,
          };

          let formData = new FormData();
          let formDocumentos = [];
          var data = {}
          for (let index = 0; index < this.lstDocumentos.length; index++) {
            if (this.lstDocumentos[index].codigo == 0) {
              formData.append("file", this.lstDocumentos[index].documento);
              data = {
                codigo: this.lstDocumentos[index].codigo,
                titulo: this.lstDocumentos[index].titulo,
                descripcion: this.lstDocumentos[index].descripcion,
              };
            formDocumentos.push(data);
            }
          }

          formData.append("documento", JSON.stringify(formDocumentos));
          formData.append("cliente", JSON.stringify(cliente));

          this.$loading(true);
          await servicio.guardarCliente(formData).then((r) => {
            if (r.value) {
              var codUltimoCliente = r.data[0];
              this.$swal.fire({
                text: `El cliente ${this.form.nombre} se ha guardado exitosamente!`,
                icon: "success",
              });

              if (codUltimoCliente > 0) {
                this.id = codUltimoCliente;
              }

              this.mostrarDirecciones = true;
              this.activeDirecciones = true;
            } else {
              this.$swal.fire({
                text: `No se pudo guardar el cliente, contacte a sistemas`,
                icon: "error",
              });
            }
          });

          this.$loading(false);
        }
      }
    },

    async guardarContacto() {
      this.$loading(true);
      let contactoAGuardar = {
        codCliente: this.id,
        codContacto: this.form.contacto.codContacto,
        nombreContacto: this.form.contacto.nombreContacto,
        telefono: this.form.contacto.telefono,
        telefonoMovil: this.form.contacto.telefonoMovil,
        correo: this.form.contacto.correo,
        contactoPrincipal: this.form.contacto.contactoPrincipal,
      };
      if (
        contactoAGuardar.telefono ||
        contactoAGuardar.telefonoMovil ||
        contactoAGuardar.correo
      ) {
        await servicio.guardarContacto(contactoAGuardar).then((r) => {
          var alertType = r.value ? "success" : "error";
          this.$swal.fire({
            text: r.message,
            icon: alertType,
          });
          this.cargaCliente();
        });
        this.$loading(false);
      } else {
        this.$swal.fire({
          text: "Por lo menos proporcione un dato para el contacto",
          icon: "warning",
        });
        this.$loading(false);
      }
    },

    // vendedores
    async obtenerVendedores() {
      // this.vendedores = [{ value: 0, text: "Seleccione el vendedor" }];
      this.$loading(true);
      await servicioAgentes.agentesCombo()
        .then((r) => {
          if (r.value) {
            r.data.forEach((element) => {
              let agente = {
                value: element.codVendedor,
                text: element.nombreVendedor,
              };
              this.vendedores.push(agente);
            });
          }
      });
      this.$loading(false);
    },

    consultaTiposObras() {
      servicioMateriales.tiposObras().then((r) => {
        if (r.value) {
          r.data.forEach((element) => {
            let tipoObra = {
              value: element.codigo,
              text: element.descripcionCorta,
            };
            this.tiposObras.push(tipoObra);
          });
        }
      });
    },

    async cargaCliente() {
      if (this.id > 0) {
        this.$loading(true);

        await servicio.clientes(this.id).then((r) => {
          if (r.value) {
            console.log(r.data.cliente[0])

            this.mostrarDirecciones = true;
            this.form.nombreComercial = r.data.cliente[0].nombreComercial;
            this.form.razonSocial = r.data.cliente[0].razonSocial;
            this.form.nombre = r.data.cliente[0].nombre;
            this.form.apellidoP = r.data.cliente[0].apellidoP;
            this.form.apellidoM = r.data.cliente[0].apellidoM;
            this.form.rFC = r.data.cliente[0].rFC;
            this.form.estatus = r.data.cliente[0].estatus;
            this.form.tipoEmpleado = r.data.cliente[0].tipoEmpleado;
            this.form.codTipoCliente = r.data.cliente[0].codTipoCliente;
            this.form.alias = r.data.cliente[0].alias;
            this.form.telefono = r.data.cliente[0].telefono;
            this.form.celular = r.data.cliente[0].celular;
            this.form.correo = r.data.cliente[0].correo;
            this.tipoClienteCredito.creditoLimiteFormato =
              r.data.datosCredito[0]?.creditoFormato;
            this.tipoClienteCredito.diasPlazo =
              r.data.datosCredito[0]?.diasPlazo;
            this.form.creditoUtilizado =
              r.data.datosCredito[0]?.creditoUtilizado;
            this.form.creditoUtilizadoFormato =
              r.data.datosCredito[0]?.creditoUtilizadoFormato;

            this.form.diaRevision = r.data.cliente[0]?.diaRevision;

            this.form.codVendedor = r.data.cliente[0]?.codEmpleadoVendedor;
            this.form.regimenFiscal = r.data.cliente[0]?.regimenFiscal;

            this.form.codSegmento = r.data.cliente[0]?.codSegmento;
            this.form.codTipoCliente = r.data.cliente[0]?.codTipoCliente;
            this.form.codTipoClienteCredito = r.data.cliente[0]?.codTipoClienteCredito;
            this.form.codTipoListaPrecio = r.data.cliente[0]?.codTipoListaPrecio;

            this.form.rfcGenerico = r.data.cliente[0]?.facturarPublicoGeneral;

            //Itero sobre las direcciones que me trae del servicio para agregarlas a fiscal o a obras
            this.itemsDireccionesFiscales = [];
            this.itemsDireccionesObras = [];

            r.data?.direccionesXCliente.forEach((dir) => {
              if (dir.fiscal && !dir.entrega) {
                this.itemsDireccionesFiscales.push(dir);
              } else if (dir.entrega && !dir.fiscal) {
                this.itemsDireccionesObras.push(dir);
              }
            });

            this.notasRemisionPendientesLiquidar = r.data.notasRemision;
            this.itemsContactos = r.data.contactosXCliente;
          } else {
            this.$loading(false);

            this.$swal.fire({
              text: `${r.message}`,
              icon: "error",
            });

            setTimeout(() => {
              this.$router.push({ path: "." });
            }, 1000);
          }
        });

        this.$loading(false);
      }
    },

    async addContacto(button) {
      // let contacto = [];
      this.$root.$emit("bv::show::modal", "modalContactos", button);
    },

    /*
