    agregarDireccion(fiscal) {
      let direccion = null;

      if (!fiscal) {
        let position = this.$refs.mapObra.getPosition();
        this.form.direccionObra.latitud = position.currentLocation.lat;
        this.form.direccionObra.longitud = position.currentLocation.long;
        direccion = {
          codigo: this.form.direccionObra.codigo,
          codCliente: this.id,
          cP: this.form.direccionObra.cP,
          codEstado: this.form.direccionObra.codEstado,
          codColonia: this.form.direccionObra.codColonia,
          calleNumero: this.form.direccionObra.calleNumero,
          codTipoObra: this.form.direccionObra.codTipoObra,
          referencia: this.form.direccionObra.referencia,
          uRLGoogleMaps: this.form.direccionObra.uRLGoogleMaps,
          latitud: this.form.direccionObra.latitud,
          longitud: this.form.direccionObra.longitud,
          fiscal: this.form.direccionObra.fiscal,
          entrega: this.form.direccionObra.entrega,
        };
      } else {
        let position = this.$refs.mapFiscal.getPosition();
        this.form.direccionFiscal.latitud = position.currentLocation.lat;
        this.form.direccionFiscal.longitud = position.currentLocation.long;
        direccion = {
          codigo: this.form.direccionFiscal.codigo,
          codCliente: this.id,
          cP: this.form.direccionFiscal.cP,
          codEstado: this.form.direccionFiscal.codEstado,
          codColonia: this.form.direccionFiscal.codColonia,
          calleNumero: this.form.direccionFiscal.calleNumero,
          codTipoObra: this.form.direccionFiscal.codTipoObra,
          referencia: this.form.direccionFiscal.referencia,
          uRLGoogleMaps: this.form.direccionFiscal.uRLGoogleMaps,
          latitud: this.form.direccionFiscal.latitud,
          longitud: this.form.direccionFiscal.longitud,
          fiscal: this.form.direccionFiscal.fiscal,
          entrega: this.form.direccionFiscal.entrega,
        };
      }

      servicio
        .guardarDireccion(direccion)
        .then((r) => {
          if (r.value) {
            this.$swal.fire({
              text: r.message,
              icon: "success",
            });
            this.cargaCliente();
            this.limpiarDireccion(fiscal);
          } else {
            this.$swal.fire({
              text: `${r.message}`,
              icon: "error",
            });
          }
        })
        .catch((e) => {
          this.$swal.fire({
            text: `${e.message}`,
            icon: "error",
          });
        });
    },

    limpiarDireccion(fiscal) {
      if (fiscal) {
        this.form.direccionFiscal.cP = "";
        this.form.direccionFiscal.codEstado = 0;
        this.form.direccionFiscal.estado = "";
        this.form.direccionFiscal.codMunicipio = 0;
        this.form.direccionFiscal.municipio = "";
        this.form.direccionFiscal.codColonia = null;
        this.form.direccionFiscal.codTipoObra = 0;
        this.form.colonia = "";
        this.disabledCP = false;
        this.form.direccionFiscal.calleNumero = "";
        this.form.direccionFiscal.fiscal = true;
        this.form.direccionFiscal.entrega = false;
        this.coloniasFiscales = [
          { value: null, text: "Seleccione una colonia..." },
        ];
      } else {
        this.form.direccionObra.cP = "";
        this.form.direccionObra.codEstado = 0;
        this.form.direccionObra.estado = "";
        this.form.direccionObra.codMunicipio = 0;
        this.form.direccionObra.municipio = "";
        this.form.direccionObra.codColonia = null;
        this.form.direccionObra.codTipoObra = 0;
        this.form.colonia = "";
        this.disabledCP = false;
        this.form.direccionObra.calleNumero = "";
        this.form.direccionObra.fiscal = false;
        this.form.direccionObra.entrega = true;
        this.colonias = [{ value: null, text: "Seleccione una colonia..." }];
      }
    },
    //Obtenie los datos de un codigo postal pero se tiene que especificar si son datos fiscales o de obra
    //Se usa -> (1)  si es fiscal, sino; si es obra se usa el (0)
    obtenerDatosCP(datosFiscales) {
      this.$loading(true);
      if (!datosFiscales) {
        servicioGenerales
          .obtenerDatosCP(this.form.direccionObra.cP)
          .then((r) => { 
             console.log(r.data[0].municipio)
            if (r.value) { 
                   
                r.data.forEach((x) => {
                  this.colonias.push({ value: x.codigoColonia, text: x.colonia });
                
                });  
                this.$loading(false); 
                this.form.direccionObra.municipio = r.data[0].municipio;
                const [primerResultado] = r.data;
                const [codigoEstado, codigoMunicipio] =
                    primerResultado;
                this.form.direccionObra.codEstado = codigoEstado;
                this.form.direccionObra.codMunicipio = codigoMunicipio;
                
            
                //   this.form.direccionObraciudad = this.form.direccionObracodEstado == 10 ?  'Tamazula': this.form.direccionObraciudad;
                this.disabledCP = true;
            } else {
              this.$loading(false);
              this.$swal.fire({
                text: r.message,
                icon: "warning",
              });
            }
          });
      } else {
        this.$loading(true);
        servicioGenerales
          .obtenerDatosCP(this.form.direccionFiscal.cP)
          .then((r) => {
            if (r.value) {
              r.data.forEach((x) => {
                this.coloniasFiscales.push({
                  value: x.codigoColonia,
                  text: x.colonia,
                });
                this.$loading(false);
              });
              this.form.direccionFiscal.codEstado = r.data[0].codigoEstado;
              this.form.direccionFiscal.codMunicipio =
                r.data[0].codigoMunicipio;
              this.form.direccionFiscal.municipio = r.data[0].municipio;
            } else {
              this.$loading(false);
              this.$swal.fire({
                text: r.message,
                icon: "warning",
              });
            }
          });
      }
    },

    addTelefono() {
      this.contactos.push({
        codContacto: this.contactos.length + 1,
        telefono: "",
        correo: "",
      });
    },

    removeTelefono(index) {
      this.contactos.splice(index, 1);
    },

    cancelar() {
      let tipoActividad =
        this.id == 0
          ? "¿Desea cancelar el registro de un nuevo cliente?"
          : "¿Desea cancelar la actualización del cliente?";
      this.$swal
        .fire({
          title: tipoActividad,
          text: "",
          showCancelButton: true,
          confirmButtonColor: "#3085d6",
          cancelButtonColor: "#d33",
          confirmButtonText: "Si, volver al catálogo",
          cancelButtonText: "Cancelar",
        })
        .then((result) => {
          if (result.value) {
            window.history.back();
          }
        });
    },

    async ObtenerSegmentos() {
      this.segmentos = [
        { value: null, text: "Seleccione el tipo de segmento" },
      ];
      this.$loading(true);

      await servicio.obtenerSegmentos().then((r) => {
        r.data.forEach((element) => {
          let segmento = {
            value: element.codSegmento,
            text: element.descripcion,
          };
          this.segmentos.push(segmento);
        });
      });

      this.$loading(false);
    },

    async ObtenerTiposClientes() {
      this.tiposClientes = [
        { value: null, text: "Seleccione el tipo del cliente" },
      ];
      this.$loading(true);

      await servicio.obtenerTiposClientes().then((r) => {
        if (r.value) {
          r.data.forEach((element) => {
            let tipoCliente = {
              value: element.codTipoCliente,
              text: element.descripcion,
            };
            this.tiposClientes.push(tipoCliente);
          });
        }
      });

      this.$loading(false);
    },

    async ObtenerTiposClienteCredito() {
      this.tiposClientesCredito = [
        { value: null, text: "Seleccione el tipo del credito del cliente" },
      ];
      this.$loading(true);

      await servicio.obtenerTiposClienteCredito().then((r) => {
        if (r.value) {
          this.tiposClientesCreditoRespaldo = r.data;
          r.data.forEach((element) => {
            let tipoClienteCredito = {
              value: element.codTipoClienteCredito,
              text: element.descripcion,
            };
            this.tiposClientesCredito.push(tipoClienteCredito);
          });
        }
      });

      this.$loading(false);
    },

    async ObtenerTiposListaPrecios() {
      this.tiposListaPrecios = [
        { value: null, text: "Seleccione el tipo de lista de precios" },
      ];
      this.$loading(true);

      await servicio.obtenerTiposListaPrecios().then((r) => {
        if (r.value) {
          r.data.forEach((element) => {
            let tipoListaPrecio = {
              value: element.codTipoListaPrecio,
              text: element.descripcion,
            };
            this.tiposListaPrecios.push(tipoListaPrecio);
          });
        }
      });

      this.$loading(false);
    },

    changeTipoClienteCredito() {
      let codTemporal = this.form.codTipoClienteCredito;

      for (let i = 0; i < this.tiposClientesCreditoRespaldo.length; i++) {
        if (
          codTemporal ===
          this.tiposClientesCreditoRespaldo[i].codTipoClienteCredito
        ) {
          this.tipoClienteCredito = this.tiposClientesCreditoRespaldo[i];
          this.codTipoClienteCreditoRespaldo =
            this.tiposClientesCreditoRespaldo[i].codTipoClienteCredito;
          return;
        }
      }
    },

    async CargarDefault() {
      this.form.codSegmento = null;
      this.form.codTipoClienteCredito = null;
      this.form.codTipoListaPrecio = null;
    },

    validarRegistros() {
      var erroresEncontrados = false;

      if (this.permisoCobranza) {
        if (this.form.codSegmento === null) {
          this.hasErrorSegmento = true;
          erroresEncontrados = true;
        } else {
          this.hasErrorSegmento = false;
        }

        if (this.form.codTipoClienteCredito === null) {
          this.hasErrorTipoCreditoCliente = true;
          erroresEncontrados = true;
        } else {
          this.hasErrorTipoCreditoCliente = false;
        }

        if (this.form.codTipoListaPrecio === null) {
          this.hasErrorTipoListaPrecio = true;
          erroresEncontrados = true;
        } else {
          this.hasErrorTipoListaPrecio = false;
        }

        if (
          parseInt(this.form.diaRevision) <= 0 ||
          parseInt(this.form.diaRevision) > 31 ||
          isNaN(parseInt(this.form.diaRevision))
        ) {
          this.hasErrorDiaRevision = true;
          erroresEncontrados = true;
        } else {
          this.hasErrorDiaRevision = false;
        }
      } else {
        this.form.codSegmento = 0;
        this.form.codTipoClienteCredito = 0;
        this.form.codTipoListaPrecio = 0;
        this.form.diaRevision = 0;
      }

      this.puedeGuardarCliente = !erroresEncontrados;

      return erroresEncontrados;
    },

    async handleBlur() {
      if (
        parseInt(this.form.diaRevision) <= 0 ||
        parseInt(this.form.diaRevision) > 31 ||
        isNaN(parseInt(this.form.diaRevision))
      ) {
        this.form.diaRevision = "";
        await this.$swal.fire({
          text: "Debe de ingresar el dia de revisión correctamente.",
          icon: "warning",
        });

        document.getElementById("cboDiaRevision").focus();
      }
    },

    async VerificarPermisoCobranza() {
      this.$loading(true);
      await servicioSeguridad
        .verificaPermiso(this.usuario.idUsuario, "Cobranza")
        .then((r) => {
          this.permisoCobranza = r.data;
        });

      this.$loading(false);
    },

    async VerificarPermisoHistorial() {
      this.$loading(true);
      await servicioSeguridad
        .verificaPermiso(this.usuario.idUsuario, "Cliente-Historial")
        .then((r) => {
          this.permisoHistorial = r.data;
        });

      this.$loading(false);
    },

    async buscarHistorial() {
      await this.ObtenerHistorialCliente();
    },

    async ObtenerHistorialCliente() {
      this.$loading(true);

      this.pedidosHistorial = [];
      this.notasRemisionHistorial = [];

      await servicio
        .ObtenerHistorialCliente(
          this.id,
          this.historialFechaDesde,
          this.historialFechaHasta
        )
        .then((r) => {
          this.pedidosHistorial = r.data.pedidos;
          this.notasRemisionHistorial = r.data.notasRemision;
        });

      this.$loading(false);
    },

    verInformacion(item) {
      console.log(item);
      this.$swal.fire({
        title: "PROXIMAMENTE",
        // text: `estamos trabajando`,
        icon: "info",
      });
    },
  },
};
</script>
